.fees {
  width: 100%;
  background-color: #ebeaea61;
}
.fees img {
  width: 100%;
  height: 700px;
  object-fit: cover;
}
.feeTable {
  padding: 5rem 0;
}
.feeTable h1 {
  text-align: center;
  font-size: 3rem;
  color: #1f0e5f;
  letter-spacing: 5px;
}
table {
  overflow: hidden;
  border-collapse: collapse;
  width: 70%;
  margin: 0 auto;
}
th {
  height: 3rem;
}

th,
td {
  text-align: left;
  padding: 8px;
}

th {
  background-color: #e8e8e8;
  font-weight: bold;
}

tr:nth-child(even) {
  background-color: #e4e4e4;
}

td {
  border-bottom: 1px solid #ddd;
}
.head {
  font-weight: bold;
}

.uniform {
    width: 70%;
    text-align: center;
    margin: 0 auto;
    padding: 5rem 0;
}
.uniform h1{
    font-size: 3rem;
    letter-spacing: 5px;
    color: #1f0e5f;
}
.uniform h2{
    font-size: 2rem;
    letter-spacing: 5px;
    color: #5d6523;
}
.feesLine{
    border: 0;
    height: 1px;
    background: #333;
    background-image: linear-gradient(to right, #ccc, #333, #ccc);
    width: 100%;

}
.uniformList{
    width: 50%;
    margin:0 auto;
    text-align: left;
    background-color: #b37c0d1e;
    padding: 3rem 5rem;
   
}
.uniformList h2{
    font-size: 1.5rem;
    color: #402f7e;
}
.uniformList ol li{
    font-size: 1rem;
    list-style: circle;
    color: #0e0728;
}

