@media only screen and (max-width: 1024px) {
  .feeTable h1 {
    text-align: center;
    font-size: 1.5rem;
  }
}
@media only screen and (max-width: 768px) {
 
  .uniform {
    width: 100%;
    padding: 0 1rem;
    margin: 3rem auto;
  }
  .fees img {
    width: 100%;
    height: 400px;
    object-fit: cover;
  }
  .uniformList{
    width: 50%;
  }
}
@media only screen and (max-width: 600px) {

 
    .uniform h1{
      font-size: 2rem;
    }
    .uniform h2{
      font-size: 1.5rem;
    }
    .uniformList h2{
        font-size: 1rem;
    }
    .uniformList ol li{
        font-size: .7rem;
    }
}


