.readmore{
    background-color: #ebeaea61;
}

.readMore{
    margin: 0 auto;
    width: 100%;
    height: 700px;
    max-height: 100%;
    
}
.readMore p{
    font-size: 1.3rem;
    line-height: 2;
    width: 70%;
    margin: auto;
    padding: 5rem ;
}
.readmoreBanner{
    width: 100%;
    height:500px;
}
.readmoreBanner img{
    width: 100%;
    height: 100%;
    object-fit: fill;
}
@media screen and (max-width: 1200px){
    .readMore p{
        font-size: 1rem;
    }
}

@media screen and (max-width: 768px){
    .readMore p{
        font-size: .7rem;
    }
    .readmoreBanner{
        height:100%;
    }
}